<template>
  <div id="register_view">
  	<template v-if="isComplete">
      <h1 class="title"></h1>
      <form method="post" onsubmit="return false" id="registerSuccess">
        <p class="tip">Token</p>
        <div class="form-alias">
          <label>
            <strong>token</strong>
            <input
              v-model="token"
              type="text"
              name="token"
              placeholder="token">
          </label>
        </div>
        <div class="form-submit">
          <router-link class="submit" :to="{ name: 'LoginView'}" tag="button">
            
          </router-link>
        </div>
      </form>
    </template>
    <template v-else>
	  	<h1></h1>
	  	<p  v-if="error" class="error">{{error}}</p>
	  	<form action="" method="post" id="register" @submit.prevent="onSubmit()">
	  		<input type="text" name="email" id="email" v-model.trim="email" placeholder="" @focus="onFocus"/>
	  		<input type="password" name="password" id="password" v-model.trim="pass" placeholder="" @focus="onFocus"/>
	  		<input type="text" name="userName" id="userName" v-model.trim="name" placeholder="" @focus="onFocus"/>
	  		<button class="submit" type="submit" :disabled="isDisabled"  :class="{disabled: isDisabled}" id="registerBtn">
            {{registerState}}
         </button>
	  	</form>
	  	<div class="footer">
	    	<div class="agreement"></div>
	    	<div class="btns">
	      	<a href="#">App</a>
	    	</div>
		  </div>
  	</template>
  </div>
</template>

<script>
export default {
  name: 'RegisterView',
  components: { },
  data () {
    return {
      isComplete: false,        // Registration completed
      isDisabled: false,        // Disabled submit button
      isShow: 0,                // Show pwd
      registerState: '',
      passType: 'password',     // Password input type
      error: '',                // Verification results
      email: '',
      pass: '',
      name: '',
      token: ''
    }
  },
  methods: {
    beforeSubmit: function () {
      // console.log('Submiting...')
      this.isDisabled = true
      this.registerState = '...'
    },
    onSuccess: function (res) {
      // console.log('complete!')
      this.isComplete = true
      this.token = res.body.token
      localStorage.setItem('token', this.token)
    	localStorage.setItem('email', this.email)
    	localStorage.setItem('name', this.name)
    },
    onError: function (err) {
      this.error = err.body.error
      this.registerState = ''
      this.isDisabled = false
    },
    onSubmit: function () {
      // Disabled submit button
      this.beforeSubmit()
      	// Regist...
      if(this.email && this.pass && this.name){
      		this.$http.post('https://douban.herokuapp.com/user/',{
		        email: this.email,
		        pass: this.pass,
		        name: this.name
		      }).then(function(res){
			    		this.onSuccess(res)
					},function(err){
						this.onError(err)
					});
      }else{
      	this.error = ''
      	this.registerState = ''
      	this.isDisabled = false
      }
    },
    onFocus:function(){
    	this.error = false;
    }
  }
}
</script>

<style lang="scss" scoped>
	#register_view{
		width: 100%;
		h1{
			font-size: 0.8rem;
			color: #42bd56;
			text-align: center;
			margin: 0.9rem;
		}
		#registerSuccess{
			padding: 0.48rem 0.58rem;
			p{
				font-size: 0.28rem;
				color: #aaa;
				margin: 0.28rem;
			}
			.form-alias{
				float: left;
				width: 100%;
				strong{
					display: none;
				}
				input{
					float: left;
			    width: 100%;
			    height: 1.02rem;
			    padding: 0.16rem;
			    box-sizing: border-box;
			    font-size: 0.3rem;
			    background: #fff;
			    border: 1px solid #ccc;
			    border-top-left-radius: 0.06rem;
			    border-top-right-radius: 0.06rem;
			    outline: 0;
			    background-color: rgb(250, 255, 189);
				}
			}
			.form-submit{
				float: left;
				width: 100%;
				height: 1.02rem;
				margin-top: 0.24rem;
				.submit{
					float: left;
					width: 100%;
					height: 100%;
					text-align: center;
					font-size: 0.34rem;
					outline: none;
					background: #42bd56;
    			border: 1px solid #17aa52;
    			color: white;
		    	border-radius: 0.06rem;
				}
			}
		}
		.error{
			color: red;
			font-size: 0.3rem;
			padding-left: 0.75rem;
		}
		#register{
			float: left;
			width: 100%;
			padding:0.55rem;
			input{
				width: 100%;
				height: 1rem;
				border: 1px solid #cccccc;
				font-size: 0.3rem;
				padding-left: 0.2rem;
				border-radius: 0.1rem;
				outline: none;
				float: left;
				margin-top: 0.24rem;
			}
			#registerBtn{
				width: 100%;
				height: 1rem;
				background: #42bd56;
				outline: none;
				border: none;
				color: white;
				font-size: 0.3rem;
				float: left;
				border-radius: 0.1rem;
				margin-top: 0.24rem;
			}
			#registerBtn.disabled {
	      cursor: not-allowed;
	      background: #eee;
	      border: none;
	    }
		}
		.footer {
	    .agreement {
	      font-size: 0.28rem;
	      color: #aaa;
	      text-align: center;
	    }
	
	    .btns {
	      margin-top: 0.8rem;
	      text-align: center;
	      font-size: 0.3rem;
	      a {
	        color: #42bd56;
	        margin-right: 0.3rem;
	      }
	    }
	  }
	}
</style>



