<template>
	<router-link class="user-bar" :to="{ name: this.currentLink}" tag="div">
		<div class="avatar">
			<template v-if="this.currentUser.name">
				<img src="../assets/img/avatar.png" alt="avatar">
			</template>
			<template v-else>
				<img src="../assets/img/user_normal.jpg" alt="">
			</template>
		</div>
		<div class="holder">{{this.holder}}</div>
		<div class="icon icon-camera"></div>
		<div class="icon icon-pen"></div>
	</router-link>
</template>

<script>
	export default {
		name: 'List',
		props: {
			
		},
		data() {
			return {
				currentLink:'',
				holder:'',
				currentUser:{
					name:'',
					email:'',
					token:''
				}
			}
		},
		created () {
		    // Get local user filling store/user
		    if (localStorage.getItem('email') && localStorage.getItem('token') && localStorage.getItem('name')) {
		      this.currentUser.email = localStorage.getItem('email');
		      this.currentUser.name = localStorage.getItem('name');
		      this.currentUser.token = localStorage.getItem('token');
		      this.currentLink = 'HomeView';
		      this.holder = this.currentUser.name;
		    }else{
		    	this.currentLink = 'LoginView';
		       this.holder = '';
		    }
		}
	}
</script>

<style lang='scss' scoped>
.user-bar {
  position: relative;
  padding: 0.2rem 0.36rem;
  overflow: hidden;

  .avatar {
    width: 0.8rem;
    height: 0.8rem;
    float: left;

    img {
      width: 100%;
      height: 100%;
      border-radius: 100%;
      display:inherit;
    }
  }

  .holder {
    float: left;
    padding-left: 0.2rem;
    line-height: 0.8rem;
    font-size: 0.3rem;
    color: #aaa;
  }

  .icon {
    content: '';
    width: 0.8rem;
    height: 0.8rem;
    padding: 0.16rem;
    margin-left: 0.16rem;
    float: right;
    box-sizing: border-box;
  }

  .icon::before {
    content: '';
    position: absolute;
    width: 0.48rem;
    height: 0.48rem;
    background-position: center;
    background-repeat: no-repeat;
  }

  .icon-camera::before {
    background-image: url(../assets/img/camera.svg);
  }

  .icon-pen::before {
    background-image: url(../assets/img/pen.svg);
  }

  &:before {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 0.02rem;
    background: #E8E8E8;
  }
}
</style>



