<template>
  <div class="page">
  	<scroller title="" type="hasCover" :items="novel"></scroller>
    <scroller title="" type="hasCover" :items="reality"></scroller>
    <scroller title="" type="hasCover" :items="travel">
    	<div class="promItem" slot="promItem">
        <img class="corver" src="../assets/img/book_zw.jpg" alt="">
        <div class="content">
          <span class="price"> 68</span>
          <p class="name"></p>
          <p class="info">3000</p>
        </div>
      </div>
    </scroller>
    <scroller title="" type="onlyString" :items="bookTags"></scroller>
    <type></type>
  	<download-app></download-app>
  </div>
</template>

<script>
import { mapState } from 'vuex'	
import Scroller from '../components/Scroller'	
import Type from '../components/Type'
import DownloadApp from '../components/DownloadApp'
export default {
  name: 'BookView',
  components: { Scroller,Type, DownloadApp},
  data () {
    return {
    }
  },
  computed: {
    ...mapState({
      novel: state => state.book.novel,
      reality: state => state.book.reality,
      travel: state => state.book.travel,
      bookTags: state => state.book.bookTags
    })
  },
  methods:{
  	getBook: function () {
      this.$store.dispatch('getBook')
    }
  },
  created: function created () {
    this.getBook()
  }
  
}
</script>

<style scoped>
	.promItem {
		overflow: hidden;
		margin: 0.32rem 0.36rem 0.16rem 0;
	}
	
	.corver {
		float: left;
		width: 2rem;
		margin-right: 0.3rem;
	}
	

	
	.name {
		font-size: 0.38rem;
		color: #494949;
		margin: 0.2rem;
		max-width: 100%;
		line-height: 0.44rem;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		word-wrap: normal;
	}
	
	.price {
		float: right;
		color: #E76648;
		font-size:0.32rem;
		line-height: 0.44rem;
	}
	
	.info {
		font-size: 0.26rem;
		font-weight: 300;
		line-height: 1.5;
		color: #9B9B9B;
	}
</style>



