<template>
	<div class="type">
		<h2></h2>
		<ul>
			<li v-for="item in items">
				<a :href="baseUri + item.href">
					{{item.title}}
					<span></span>
				</a>
			</li>
			<li></li>
		</ul>
	</div>
</template>

<script>
	export default {
		name: 'List',
		data() {
			return {
				baseUri: 'https://m.douban.com/',
      			items: [
			        {
			          title: '',
			          href: 'movie/classic'
			        },
			        {
			          title: '',
			          href: 'movie/underrated'
			        },
			        {
			          title: '',
			          href: 'movie/doubantop'
			        },
			        {
			          title: '',
			          href: 'movie/action'
			        },
			        {
			          title: '',
			          href: 'movie/comedy'
			        },
			        {
			          title: '',
			          href: 'movie/love'
			        },
			        {
			          title: '',
			          href: 'movie/mystery'
			        },
			        {
			          title: '',
			          href: 'movie/horror'
			        },
			        {
			          title: '',
			          href: 'movie/scifi'
			        },
			        {
			          title: '',
			          href: 'movie/sweet'
			        },
			        {
			          title: '',
			          href: 'movie/artfilm'
			        },
			        {
			          title: '',
			          href: 'movie/youth'
			        },
			        {
			          title: '',
			          href: 'movie/animation'
			        },
			        {
			          title: '',
			          href: 'movie/chinese'
			        },
			        {
			          title: '',
			          href: 'movie/western'
			        },
			        {
			          title: '',
			          href: 'movie/korean'
			        },
			        {
			          title: '',
			          href: 'movie/japanese'
			        }
			    ]
			}
		},
		filters: {
			
		}
	}
</script>

<style lang='scss' scoped>
.type{
	float: left;
	width: 100%;
	h2{
		font-size: 0.34rem;
		font-weight: 400;
		margin: 0.5rem 0.56rem;
	}
	ul{
		float: left;
		margin:0 0.5rem;
		font-size: 0.3rem;
		border-bottom: solid 1px #eee;
		li{
			float: left;
			width:50%;
			height: 1rem;
			line-height: 1rem;
			border-top: 1px solid #eee;
			border-right: 1px solid #eee;
			a{
				display: block;
				color: #42bd56;
				span{
					color: #ccc;
				    float: right;
				    font-weight: bold;
				    display: inline-block;
				    border-right: solid 1px #ccc;
				    border-bottom: solid 1px #ccc;
				    width: .26rem;
				    height: .26rem;
				    transform: rotate(-45deg);
				    margin-top: 0.4rem;
				    margin-right: 0.4rem;
				}
			}
		}
		li:nth-child(even){
			border-right: none;
			padding-left: 0.34rem;
		}
	}
}
</style>



