<template>
	<div class="scroller">
		<div class="header">
			<h2>{{title}}</h2>
			<a href="#"></a>
		</div>
		<div class="content">
			<slot name="promItem"></slot>
			<ul  class="hasCover" v-if="type === 'hasCover'">
				<li v-for ="item in items">
					<router-link :to="'subject/' + item.id " append>
			            <img v-if="item.images" :src="item.images.large" alt="">
			            <span class="title">{{item.title}}</span>
			            <rating v-if="item.rating" :rating="item.rating"></rating>
		          	</router-link>
				</li>
			</ul>
			<ul  class="onlyString" v-else>
				<li v-for = "item in items" v-bind:style="{ color: item.color}">
					<a :href="item.href" v-if="!item.line" :style="{color: item.color}">{{item.title}}</a>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	import Rating from './Rating'
	export default {
		name: 'scroller',
		props: ['title', 'type', 'items'],
		components: { Rating },
		data() {
			return {}
		}
	}
</script>

<style lang='scss' scoped>
.scroller{
	float: left;
	width: 100%;
	padding: 0.24rem 0.15rem 0;
	overflow: hidden;
	.header{
		height: 0.6rem;
		width:100%;
		padding:0 0.48rem;
		line-height:0.6rem;
		h2{
			font-size: 0.34rem;
			float: left;
			font-weight: 400;
		}
		a{
			font-size: 0.30rem;
			color: #42bd56;
			float: right;
		}
	}
	.content{
		width: 100%;
		padding-left: 0.48rem;
		.hasCover {
			padding:0.2rem 0 1rem 0;
			margin-bottom: -1rem;
			overflow-x: auto;
  			white-space: nowrap;
			li{
				width: 2rem;
				display: inline-block;
				margin-right:0.15rem;
				a{
					float: left;
					width: 100%;
					img{
						width: 2rem;
						height: 2.8rem;
						display: inherit;
					}
					span{
						width: 100%;
						text-align: center;
						font-size: 0.3rem;
						color: #111;
						float: left;
						overflow: hidden;
						text-overflow:ellipsis;
						white-space: nowrap;
						line-height: 0.6rem;
					}
				}
			}
		}
		.onlyString{
			overflow-x: auto;
  			white-space: nowrap;
  			padding:0.2rem 0 1rem 0;
			margin-bottom: -1rem;
			li{
				box-sizing: content-box;
				display: inline-block;
			    margin: 0 0.16rem 0.1rem;
			    font-size: 0.32rem;
			    border-radius: 0.08rem;
			    vertical-align: middle;
			    border: 1px solid;
			    a{
			    	height: 1rem;
				    line-height: 1rem;
				    padding: 0 0.48rem;
				    overflow: auto;
				    display: block;
				    text-align: center;
			    }
			}
			li:empty {
			    width: 100%;
			    display: block;
			    height: 0rem;
			    margin: 0;
			    border: 0;
			}
		}
	}
	
}
</style>



