<template>
	<div class="banner">
		<div class="bg">
			<img src="../assets/img/promotion_bg.jpg"/>
		</div>
		<p>{{title}}<span><a href="javascript:;"></a><a href="javascript:;"></a></span></p>
	</div>
</template>

<script>
	export default {
		name: 'banner',
		props: {
			title: {
		      type: String,
		      required: false,
		      default: 'App, '
		    }
		},
		data() {
			return {}
		}
	}
</script>

<style lang='scss' scoped>
.banner{
	width: 100%;
	height: 1.6rem;
	line-height: 1.6rem;
	position: relative;
	.bg{
		width: 100%;
		height: 100%;
		position:absolute;
		left:0;
		top:0;
		img{
			width: 100%;
			height: 100%;
		}
	}
	p{
		position: absolute;
		width: 100%;
		height: 100%;
		left: 0;
		top: 0;
		z-index: 99;
		font-size: 0.28rem;
		padding-left: 0.36rem;
		color: #2ca532;
		span{
			float: right;
			a{
				display: inline-block;
				text-align: center;
				width: 1.64rem;
				height: 0.56rem;
				line-height: 0.56rem;
				border:1px solid #42bd56;
				margin-right: 0.2rem;
			}
			a:nth-child(1){
				background: #42bd56;
				color: white;
			}
			a:nth-child(2){
				border:1px solid #42bd56;
				color: #42bd56;
				
			}
		}
	}
}
</style>



