<template>
	<div class="list">
		<template v-if="mold === 'thumbnail'" v-for="item in items">
			<router-link class="thumbnail" :to="{name: 'DetailView', params: { id: item.id }}">
				<div class="content">
					<img :src="item.image_hlarge" alt="cover">
					<h3>{{item.title}}</h3>
					<p>{{item.content | subStr}}</p>
				</div>
				<div class="author">
					<span class="name">{{item.category_name}}</span>
					<span class="label" v-if="item.subcategory_name">
        				 {{item.subcategory_name}}
          			</span>
				</div>
			</router-link>
		</template>
		<template v-if="mold === 'basic'">
	      <ul class="basic">
	        <li v-for="item in items">
	          <a href="#">
	            <h3>{{item.title}}</h3>
	            <div class="info">{{item.comments}}</div>
	          </a>
	        </li>
	      </ul>
	    </template>
	</div>
</template>

<script>
	export default {
		name: 'List',
		props: {
			mold: {
		      type: String,
		      default: 'basic'
		    },
			items: {
				type: Array,
				required: true
			}
		},
		data() {
			return {}
		},
		filters: {
			subStr: function(value) {
				value.replace(/<.+>|\s/, '')
				return value.slice(0, 30)
			}
		}
	}
</script>

<style lang='scss' scoped>
.list {
	float: left;
	width: 100%;
  .thumbnail {
    position: relative;
    display: block;
		padding:0.6rem 0;
		border-bottom:1px solid #eee;
    .content {
      overflow: hidden;
     
    }

    h3 {
      margin-top: 0;
	    margin-bottom: 0.14rem;
	    line-height: 1.41;
	    text-align: justify;
	    font-size: 0.34rem;
	    font-weight: 500;
	    color: #494949;
    }

    p {
     
      text-align: justify;
      color: #aaa;
      font-size: 0.24rem;
      line-height: 0.38rem;
      overflow: hidden;
    }

    img {
      float: right;
    	width: 25.6%;
   		height: 2.1rem;
    	margin-left: 0.6rem;
    }

    .author {
    	font-size:0.24rem;
      color: #ccc;
      margin-top: 0.52rem;
    }

    .label {
      position: absolute;
      right: 0;
    }
  }

  .thumbnail ~ .thumbnail::before {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
   
    content: '';
    background: #e3e3e3;
  }

  .basic {
  	
    h3 {
      padding: 0.34rem 0;
      line-height: 1.41;
      font-size: 0.34rem;
      font-weight: 500;
      color: #494949;
    }

    .info {
      margin-top: 0.1rem;
      font-size: 0.28rem;
      color: #42bd56;
    }
  }
}
</style>



