<template>
  <div class="tags">
    <ul>
      <li v-for="item in items">
        <a href="#">{{item.name ? item.name : item}}</a>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'tags',
  props: {
    items: {
      type: Array,
      required: true
    }
  },
  data () {
    return {}
  }
}
</script>

<style lang="scss" scoped>
.tags {
	float: left;
	width: 100%;
	margin: 0.4rem 0;
	ul{
		float: left;
		width: 100%;
	}
  li {
    float: left;
    margin: 0.2rem 0.2rem 0 0;
    font-size: 0.3rem;
  }

  a {
    display: block;
    padding: 0 0.24rem;
    line-height: 0.56rem;
    font-size: 0.3rem;
    border-radius: 0.56rem;
    text-align: center;
    color: #494949;
    background: #f5f5f5;
  }
}
</style>



