<script>
import SubNav from '../components/SubNav'

export default {
  name: 'talion-view',
  components: { SubNav },
  data () {
    return {
      queryStr: ''
    }
  },
  methods: {
    closeTalion: function () {
      this.$emit('closeTalion')
    },
    goSearch: function () {
      this.$emit('closeTalion')
      this.$router.push({
        name: 'SearchView',
        params: {
          q: this.queryStr
        }
      })
    }
  }
}
