<template>
  <div class="group">
    <div class="header">
      <h2>{{title}}</h2>
    </div>
    <ul class="content">
      <li v-for="item in items">
        <router-link
          :to="{name: 'HomeView'}">
          <div class="group-meta">
            <img v-if="item.images.small" :src="item.images.small" alt="cover">
            <div class="group-info">
              <span>{{item.title}}</span>
              <rating v-if="item.rating" :rating="item.rating"></rating>
            </div>
            <span v-if="item.group_member" class="group-member">20005</span>
          </div>
          <div v-if="item.group_topic" class="group-topic">
            <span>{{item.group_topic.time}}</span>
            <span>{{item.group_topic.title}}</span>
          </div>
        </router-link>
      </li>
    </ul>
    <slot name="more"></slot>
  </div>
</template>

<script>
import Rating from '../components/Rating'

export default {
  name: 'group',
  components: { Rating },
  props: {
    title: {
      type: String,
      required: true
    },
    items: {
      type: Array,
      required: true
    }
  },
  data () {
    return {}
  }
}
</script>

<style lang="scss" scoped>
.group {
  margin-left: 0.36rem;
  padding-top: 0.28rem;
  .header{
  	h2{
  		font-size: 0.36rem;
  		font-weight: 400;
  	}
  }
}

.content {
  position: relative;

  a {
    display: block;
    padding: 0.3rem 0.36rem 0.3rem 0;
  }

  .group-meta {
    display: flex;
    align-items: center;

    img {
      width: 0.8rem;
    }
  }

  .group-info {
    flex: 1;
    padding: 0 0.2rem;
    font-size: 0.36rem;
    color: #111;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .group-member {
    color: #ccc;
    font-size: 0.3rem;
  }

  .group-topic {
    margin-top: 0.2rem;
    font-size: 0.3rem;
    color: #aaa;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

li  {
  border-bottom: 1px solid #F2F2F2;
  overflow: hidden;
}
.list-link{
	float: left;
	width: 100%;
	height: 0.88rem;
	line-height: 0.88rem;
	border-bottom: 1px solid #f5f5f5;
	text-align: center;
	font-size:0.3rem ;
	color: #42bd56;
	margin-bottom: 0.32rem;
}
</style>



