<template>
	<div class="page">
		<banner title=""></banner>
		<template v-if="!showLoading">
			<div class="info">
				<h2>{{eventItem.title}} <span>{{eventItem.loc_name}}</span></h2>
				<div class="poster">
					<img :src="eventItem.image_hlarge" alt=""/>
				</div>
				<div class="detail">
					<span>:&nbsp;&nbsp;</span>
					<ul>
						<li>{{eventItem.begin_time}}</li>
						<li>{{eventItem.end_time}}</li>
					</ul>
				</div>
				<div class="detail">
					<span>:&nbsp;&nbsp;</span>
					<ul>
						<li>{{eventItem.address}}</li>
					</ul>
				</div>
				<div class="detail">
					<span>:&nbsp;&nbsp;</span>
					<ul>
						<li>{{eventItem.fee_str}}</li>
					</ul>
				</div>
				<div class="detail">
					<span>:&nbsp;&nbsp;</span>
					<ul>
						<li>{{eventItem.category_name}}</li>
					</ul>
				</div>
				<div class="detail">
					<span>:&nbsp;&nbsp;</span>
					<ul>
			            <li v-if="eventItem.owner">{{eventItem.owner.name}}</li>
		            </ul>
				</div>
				<tag v-if="eventItem.tags" :items="eventItem.tags | toArray"></tag>
				<div class="describe">
					<h2></h2>
					<div v-if="eventItem.content" class="content" v-html="content"></div>
				</div>
			</div>
			<download-app></download-app>
		</template>
    	<loading v-show="showLoading"></loading>
	</div>

</template>

<script>
import { mapState } from 'vuex'
import Banner from '../components/Banner'	
import DownloadApp from '../components/DownloadApp'
import Loading from '../components/Loading'
import Tag from '../components/Tag'
export default {
  name: 'DetailView',
  components: { Banner,DownloadApp,Loading,Tag },
  data () {
    return {
      showLoading: true
    }
  },
  computed: {
    content: function () {
      // Careful XSS
      // Remove copyright imgs
      return this.eventItem.content.replace(/<img.+?>/ig, '')
    },
    ...mapState({
      eventItem: state => state.activities.eventItem
    })
  },
  filters: {
    toArray (value) {
      return value.split(',')
    }
  },
  created () {
   	const id = this.$route.params.id
    this.$store.dispatch({
      type: 'getSingleEvent',
      id: id
    }).then(res => {
      // Success handle
      this.showLoading = false
    })
  }
}
</script>

<style lang="scss" scoped>
.info {
	padding: 0 0.4rem;
	font-size: 0.28rem;
  h2{
  	font-size: 0.34rem;
  	margin: 0.48rem 0.4rem;
  	span{
  		width: 0.72rem;
  		text-align: center;
  		height: 0.46rem;
  		line-height: 0.46rem;
  		display: inline-block;
  		background: #ff8263;
  		font-size: 0.24rem;
  		color: white;
  		border-radius: 0.06rem;
  	}
  }
  .poster{
  	padding: 0.48rem 0.76rem;
  	img{
  		width: 100%;
  	}
  }
  .detail{
  	margin-left: 0.8rem;
  	margin-bottom: 0.24rem;
  	span{
  		float: left;
  		color: #666;
  		margin-left: -0.8rem;
  	}
  	ul{
  		li{
  			line-height: 0.42rem;
  		}
  	}
  }
  .describe{
  	font-size: 0.28rem;
  	h2{
  		color: #072;
  		font-size: 0.34rem;
  	}
  }
}


</style>



