<template>
  <div class="page">
    <banner title=''></banner>
     <template v-if="!showLoading">
     	<div class="subject-card">
        <h1 class="title">{{subject.title}}</h1>
        <div class="subject-info">
          <div class="right">
            <a href="#">
              <img v-if="subject.images" :src="subject.images.large" alt="cover">
            </a>
          </div>
          <div class="left" v-if="subject.rating">
            <rating :rating="subject.rating">
              <span slot="ratingsCount">{{subject.ratings_count}}</span>
            </rating>
            <template v-if="subject.genres && subjectMeta">
              <p class="meta">{{subjectMeta}}</p>
              <a href="#" class="open-app">App</a>
            </template>
            <template v-if="subject.author && subjectMeta">
              <p class="meta">{{subjectMeta}}</p>
              <a href="#" class="buy"></a>
            </template>
          </div>
        </div>
        <div v-if="subject.author" class="vendors-link">
          <a class="link">
            
            <span class="info">
               66.00
            </span>
          </a>
        </div>
        <marking>
          <template slot="book" v-if="subject.author">
            <router-link :to="{ name: 'LoginView'}"></router-link>
            <router-link :to="{ name: 'LoginView'}"></router-link>
            <router-link :to="{ name: 'LoginView'}"></router-link>
          </template>
          <template slot="movie" v-else>
            <router-link :to="{ name: 'LoginView'}"></router-link>
            <router-link :to="{ name: 'LoginView'}"></router-link>
          </template>
        </marking>
        <div class="subject-intro">
          <h2>{{subject.title}}</h2>
          <p>
            <template v-if="summary && subject.summary">
              {{isExpand ? summary : subject.summary}}
            </template>
            <a href="javascript:;" v-show="isExpand" v-on:click="expand">
              ()
            </a>
          </p>
        </div>
        <div class="genres">
          <h2></h2>
          <template v-if="genres">
            <tag :items="genres"></tag>
          </template>
        </div>
        <div class="subject-pics">
          <h2>{{subject.title}}</h2>
          <ul v-if="subject.images">
            <li class="pic">
              <a href="#">
                <img :src="subject.images.large" alt="poster">
              </a>
            </li>
            <li class="pic">
              <a href="#">
                <img :src="subject.images.large" alt="poster">
              </a>
            </li>
            <li class="pic">
              <a href="#">
                <img :src="subject.images.large" alt="poster">
              </a>
            </li>
          </ul>
        </div>
      </div>
      <div class="subject-comments">
        <h2>{{subject.title}}</h2>
        <div class="content-list">
          <card mold="comment" v-for="item in items" :key="item"></card>
          <a class="list-link" href="javascript:;"></a>
        </div>
      </div>
     
      <div class="subject-question">
        <h2>{{subject.title}}</h2>
        <list :items="questions"></list>
        <a class="list-link" href="javascript:;"></a>
      </div>
     	<scroller title="" type="onlyString" :items="movieTags"></scroller>
     	<download-app></download-app>
     </template>
     <loading v-show="showLoading"></loading>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'	
import Banner from '../components/Banner'
import Rating from '../components/Rating'
import Loading from '../components/Loading'
import DownloadApp from '../components/DownloadApp'
import Scroller from '../components/Scroller'
import Tag from '../components/Tag'
import List from '../components/List'
import Card from '../components/Card'
import Marking from '../components/Marking'
export default {
  name: 'SubjectView',
  components: { Banner,Rating,Loading,DownloadApp,Scroller,Tag,List,Card,Marking},
  data () {
    return {
    	showLoading: true,
      isExpand: true,
      items: new Array(5)
    }
  },
  computed: {
    ...mapState({
      subject: state => state.subject.subject,
      adImgUrl: state => state.subject.adImgUrl,
      questions: state => state.subject.questions,
      movieTags: state => state.movie.movieTags
    }),
    ...mapGetters({
      subjectMeta: 'subjectMeta',
      summary: 'summary',
      genres: 'genres'
    })
  },
  methods: {
    expand: function (event) {
      this.isExpand = false
    }
  },
  created () {
    const id = this.$route.params.id
    const classify = this.$route.params.classify

    this.$store.dispatch({
      type: 'getSingleSubject',
      id: id,
      classify: classify
    }).then(res => {
      // Success handle
      this.showLoading = false
    })
  }
}
</script>

<style lang="scss" scoped>
.subject-card {
  padding: 0 0.36rem;

  h1 {
    margin: 0.4rem 0 0;
    font-size: 0.4rem;
  }
}

.subject-info {
  overflow: hidden;
  margin-bottom: 0.6rem;

  .right {
    float: right;

    img {
      display: block;
      width: 100%;
      max-width: 2rem;
    }
  }

  .left {
    margin-right: 2rem;

    .meta {
      margin-top: 0.3rem;
      padding-right: 0.48rem;
      line-height: 1.6;
      font-size: 0.28rem;
      color: #494949;
    }

    .open-app {
      display: block;
      margin-top: 0.2rem;
      line-height: 1;
      font-size: 0.28rem;
      color: #42bd56;
    }

    .buy {
      display: inline-block;
      height: 0.48rem;
      padding: 0 0.12rem;
      line-height:0.48rem;
      text-align: center;
      font-size: 0.26rem;
      color: #E76648;
      border: 1px solid #E76648;
      border-radius: 0.06rem;
    }
  }
}

.vendors-link {
  position: relative;
  margin: 0.3rem 0;
  padding: 0.2rem 0.36rem 0.2rem 0;
  line-height: 0.48rem;
  font-size: 0.3rem;
  overflow: auto;
  box-sizing: border-box;

  .link {
    display: inline-block;
    width: 100%;
    position: relative;
  }

  .info {
    position: absolute;
    top: 0;
    right: 0;
    display: inline-block;
    color: #ccc;
    font-size: 0.28rem;
  }

  &::before {
    left: 0;
    top: 0;
    width: 100%;
    height: 1px;
    background: #E8E8E8;
    content: '';
    position: absolute;
  }

  &::after {
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1px;
    background: #E8E8E8;
    content: '';
    position: absolute;
  }
}

.subject-intro, .genres, .subject-pics, .subject-comments,
.ad, .subject-question {
  margin-bottom: 0.6rem;
}

h2 {
  margin: 0 0 0.3rem;
  font-size: 0.3rem;
  color: #aaa;
}

.subject-intro {
  p {
    font-size: 0.3rem;
    color: #494949;
  }

  a {
    color: #42bd56;
  }
}

.subject-pics {
  ul {
    margin-right: -0.36rem;
    overflow-x: auto;
    white-space: nowrap;
  }

  li {
    height: 2.4rem;
    overflow: hidden;
    display: inline-block;
  }

  img {
    width: 3.6rem;
  }
}

.subject-comments h2, .subject-question {
  padding: 0 0.36rem;
}

.subject-comments, .subject-question {
  .list-link {
    display: block;
    padding: 0.3rem 0;
    font-size: 0.32rem;
    line-height: 0.35rem;
    text-align: center;
    color: #42bd56;
  }
}

.ad {
  margin: 0.6rem 0.36rem;
  margin-top: -0.4rem;
}
</style>



