<template>
	<div class="rating">
		<template v-if="full === 0">
			<span></span>
		</template>
		<template v-else>
			<template v-for="n in full">
				<span class="star-full"></span>
			</template>
			<template v-for="n in half">
				<span class="star-half"></span>
			</template>
			<template v-for="n in gray">
				<span class="star-gray"></span>
			</template>
			<span class="average">{{rating.average}}</span>
		</template>
	</div>
</template>
<script>
export default {
  name: 'Rating',
  props: {
   	rating: {
      	type: Object,
      	required: true
    }
  },
  data () {
    return{
		full: 0,
      	half: 0,
     	gray: 0
    }
  },
  created:function(){
  	let average = this.rating.average;
  	this.full = parseInt(average / 2)
    this.half = average % 2 === 0 ? 0 : 1
    this.gray = 5 - this.full - this.half
  },
  methods:{
  	
  }
}
</script>

<style lang="scss" scoped>
.rating {
	float: left;
	width: 100%;
	text-align: center;
	font-size: 0.24rem;
	line-height: 0.26rem;
	color: #aaa;
}

.star-full {
	display: inline-block;
	width: 0.26rem;
	height: 0.26rem;
	background-image: url(data:image/png;base64,);
	background-size: 0.26rem 0.26rem;
	vertical-align:middle;
}

.star-gray {
	display: inline-block;
	width: 0.26rem;
	height: 0.26rem;
	background-image: url(data:image/png;base64,);
	background-size: 0.26rem 0.26rem;
	vertical-align:middle;
}

.star-half {
	display: inline-block;
	width: 0.26rem;
	height: 0.26rem;
	background-image: url(data:image/png;base64,);
	background-size: 0.26rem 0.26rem;
	vertical-align:middle;
}

.average {
	vertical-align: middle;
	height: 0.26rem;
}
</style>



