<template>
  <div class="page">
  	<user-bar></user-bar>
  	<group title='' :items="group_a">
  		<a class="list-link" href="#" slot="more"></a>
  	</group>
  	<group title="" :items="group_b">
      <a class="list-link" href="#" slot="more"></a>
    </group>
    <group title="" :items="group_c">
      <a class="list-link" href="#" slot="more"></a>
    </group>
  	<download-app></download-app>
  </div>
</template>

<script>
import { mapState } from 'vuex'	
import UserBar from '../components/UserBar'
import Group from '../components/Group'
import DownloadApp from '../components/DownloadApp'	
export default {
  name: 'GroupView',
  components: {UserBar, Group,DownloadApp},
  data () {
    return {
    	
    }
  },
  computed: {
    ...mapState({
      group_a: state => state.group.group_a,
      group_b: state => state.group.group_b,
      group_c: state => state.group.group_c
    })
  },
   created: function () {
    this.$store.dispatch('getGroup')
  }
}
</script>

<style scoped>
	
</style>



