<template>
	<div class="sub-nav">
		<div class="navBottom" v-if="mold === 'navBottom'">
			<ul>
				<li><router-link :to="{name: 'RegisterView'}"></router-link></li>
				<li>
					<template v-if="this.email && this.token">
			          <a href="#" @click.prevent="logout()"></a>
			        </template>
			        <template v-else>
			          <router-link :to="{name: 'LoginView'}"></router-link>
			        </template>
				</li>
				<li><router-link :to="{name: 'MoveView'}"></router-link></li>
				<li><router-link :to="{name: 'MoveView'}">App</router-link></li>
			</ul>
		</div>
		<div class="indexNav" v-if="mold === 'indexNav'">
			<ul>
				<li><router-link :to="{name: 'MoveView'}"></router-link></li>
				<li><router-link :to="{name: 'MoveView'}"></router-link></li>
				<li><router-link :to="{name: 'MoveView'}"></router-link></li>
				<li><router-link :to="{name: 'MoveView'}">App</router-link></li>
			</ul>
		</div>
	</div>
</template>
<script>
export default {
  name: 'SubNav',
  props: {
    mold: {
      type: String
    }
  },
  data () {
    return{
    	email:'',
    	token:'',
    	name:''
    }
  },
   mounted:function(){
  	this.email = localStorage.getItem('email');
  	this.token = localStorage.getItem('token');
  	this.name = localStorage.getItem('name');
  },
  methods:{
  	logout:function(){
  		localStorage.removeItem('email')
	    localStorage.removeItem('token')
	    localStorage.removeItem('name')
	    this.$router.push({name: 'LoginView'})
  	}
  }
}
</script>

<style lang="scss" scoped>
.sub-nav{
	float: left;
	width: 100%;
	.indexNav{
		ul{
			float: left;
			li{
				float: left;
		    width: 50%;
		    padding: 0.06rem;
		    box-sizing: border-box;
		    font-size: 0.3ren;
		    a{
		    	background-color: #f6f6f6;
			    color: #494949;
			    display: block;
			    text-align: center;
			    padding: 0.29rem 0;
			    border-radius: 2px;
			    max-width: 100%;
			    overflow: hidden;
			    text-overflow: ellipsis;
			    white-space: nowrap;
			    word-wrap: normal;
		    }
			}
		}
	}
	.navBottom{
		padding: 0 0.15rem;
		font-size: 0.3rem;
		ul{
			width: 100%;
			float: left;
			border-bottom: 2px solid #f3f3f3;
			margin-top: 0.7rem;
			li{
				border-top: 2px solid #f3f3f3;
				height: 1.04rem;
				width: 50%;
				float: left;
				text-align: center;
				a{
					display: inline-block;
					height: 0.4rem;
					width: 100%;
					color: #42bd56;
					margin-top: 0.32rem;
				}
			}
			li:nth-child(odd) a{
				border-right: 2px solid #e5e5e5;
			}
		}
	}
}
</style>



