<template>
  <div id="login_view">
  	<h1><a href="javascript:history.go(-1);"></a></h1>
  	<p v-if="error" class="tip error">{{error}}</p>
  	<form action="" method="post" id="login" @submit.prevent="onSubmit()">
  		<input type="text" name="email" id="email"  v-model.trim="email" placeholder="" @focus = "onFocus"/>
  		<input type="password" name="token" id="token"  v-model.trim="token" placeholder="Token" @focus = "onFocus"/>
  		<button
          class="submit"
          type="submit"
          :disabled="isDisabled"
          :class="{disabled: isDisabled}">
          {{loginState}}
        </button>
  	</form>
  	<div class="footer">
	    	<p>&</p>
	    	<div class="btns">
	      	<router-link  :to="{ name: 'RegisterView'}" >
            		
          </router-link>
          <router-link  :to="{ name: 'HomeView'}" >
           		 App
          </router-link>
	    	</div>
		  </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'	
export default {
  name: 'LoginView',
  components: { },
  data () {
    return {
    	loginState: '',
      isDisabled: false,  
      error: '',
      email:'',
      token:'',
      name:''
    }
  },
  mounted:function(){
  	this.email = localStorage.getItem('email');
  	this.token = localStorage.getItem('token');
  	if(this.email && this.token){
			this.onSubmit();
  	}
  },
  methods:{
  	beforeSubmit:function(){
  		this.isDisabled = true
      this.loginState = '...'
  	},
  	onSubmit:function(){
  		this.beforeSubmit();
  		if(this.email && this.token){
  			this.$http.get('https://douban.herokuapp.com/user/'+ this.email,{
	  			headers: {
	          Authorization: 'Bearer ' + this.token
	        }
  			}).then(function(res){
			    		this.onSuccess(res)
					},function(err){
						this.onError(err)
					});
  		}else{
  			this.error = 'token'
      	this.loginState = ''
      	this.isDisabled = false
  		}
  		
  	},
  	onSuccess:function(res){
			this.name = res.body.name;
      localStorage.setItem('token', this.token)
    	localStorage.setItem('email', this.email)
    	localStorage.setItem('name', this.name)
    	this.$router.push({name: 'HomeView'})
  	},
  	onError:function(err){
  		this.error = err.body.error
      this.loginState = ''
      this.isDisabled = false
  	},
  	onFocus:function(){
  		this.error = false;
  	}
  }
}
</script>

<style lang="scss" scoped>
	#login_view{
		width: 100%;
		padding: 0.3rem;
		h1{
			width: 100%;
			height: 1rem;
			line-height: 1rem;
			margin-bottom: 0.3rem;
			font-size: 0.36rem;
			text-align: center;
			position: relative;
			a{
				font-size: 0.3rem;
				color: #42bd56;
				width: 0.6rem;
				height: 0.4rem;
				line-height: 0.4rem;
				font-weight: 400;
				position: absolute;
				left: 0.2rem;
				top: 0;
			}
		}
		.error{
			color: red;
			font-size: 0.3rem;
			padding-left: 0.75rem;
		}
		#login{
			padding: 0 0.3rem;
			input{
				width: 100%;
				height: 1rem;
				border: 1px solid #aaa;
				border-radius: 0.06rem;
				font-size: 0.3rem;
				padding-left: 0.2rem;
			}
			button{
				width: 100%;
				height: 1rem;
				border-radius: 0.06rem;
				font-size: 0.3rem;
				text-align: center;
				border: none;
				background: #17aa52;
				color: white;
			}
		}
		.footer {
	    p {
	      font-size: 0.28rem;
	      color: #aaa;
	      text-align: center;
	      margin-top: 0.5rem;
	    }
	
	    .btns {
	      margin-top: 0.8rem;
	      text-align: center;
	      font-size: 0.3rem;
	      a {
	        color: #42bd56;
	        margin-right: 0.3rem;
	      }
	    }
	  }	
	}
</style>



